@import url('https://fonts.googleapis.com/css?family=Open+Sans');

body {
  font-family: "Open Sans", serif;
  background-color: #f7fcee;
}

.wrapper {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  /*height: 55vh;*/
  background: #2a6bb1;
  box-shadow: 10px 20px 80px 10px rgba(0,0,0,0.5);
  margin: 0 auto;
}

.info {
  height: 55vh;
  background-color: #353434;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
}

#infor {
  width: 100%;
}
.form {
  height: 55vh;
  padding-top: 100px;
  padding-left: 50px;
  background: #6a6b69;
}

input[type="text"] {
  background-color: #6a6b69;
  border: 0;
  border-bottom: 1px solid #f3f9ec;
  width: 50%;
  padding-bottom: 4px;
  color: #222222;
  font-weight: lighter;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
  outline: none;
}

input[type="text"]:active {
  background-color: #6a6b69;
}

.btn_get {
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  cursor: pointer;
  background-color: #3bcb91;
  color: #fff;
  outline: none!important;
}

.btn_get:hover {
  background-color: #36b984;
}

.btn_get:active {
  background-color: #31a777;
}

.btn {
  background-color: #ff8952;
  color: white;
  font-weight: bolder;
  outline: none!important;
}

.btn:hover {
  background-color: #ff7941;
  color: white ;
}

.btn:active {
  background-color: #ff6f23;
  color: white;
}

.info h2 {
  font-size: 2.5rem;
  color: gray;
  padding: 5%;
}

.info p {
  color: #d46f39;
  font-weight: bold;
  font-size: 1.5rem;
  opacity: .6;
}


.infoWeath {
  width: 60%;
  font-size: 1.3rem;
  color: #fbfbfb;
}

.infoWeath span {
  font-weight: bold;
  color: #ffc296;
}

.error {
  color: #041f3c;
  font-weight: bold;
}

@media screen and (max-width: 375px){

  .infoWeath {
    width: 100%;
    text-align: left;
    font-size: .8rem;
    color: #fbfbfb;
  }

  .btn_get {
    height: 40px;
    box-sizing: border-box;
    padding: 2px 20px;
    font-size: 0.9rem;
  }

  form {
    display: flex;
    justify-content: center;
    /*align-items: center;*/
  }

}